* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
}

body {
  background: linear-gradient(90deg, rgb(203, 112, 207) 0%, rgba(100, 115, 255, 1) 100%);
}
.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 520px;
  min-height: 600px;
  background: #161a2b;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}

h1 {
  margin: 32px 0;
  color: #fff;
  font-size: 24px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 32px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #5d0cff;
  outline: none;
  width: 320px;
  background: transparent;
  color: #fff;
}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  outline: none;
  background: linear-gradient(90deg, rgba(93, 12, 255, 1) 0%, rgb(203, 112, 207) 100%);
  color: #fff;
  text-transform: capitalize;
}

.todo-input.edit {
  border: 2px solid #4672c8;
}

.todo-button.edit {
  background: linear-gradient(90deg, rgb(71, 116, 201) 0%, rgb(49, 31, 166) 100%);
  padding: 16px 22px;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: linear-gradient(90deg, rgba(255, 118, 20, 1) 0%, rgba(255, 84, 17, 1) 100%);

  padding: 16px;
  border-radius: 5px;
  width: 90%;
}

.todo-row:nth-child(4n + 1) {
  background: linear-gradient(90deg, rgba(93, 12, 255, 1) 0%, rgba(155, 0, 250, 1) 100%);
}

.todo-row:nth-child(4n + 2) {
  background: linear-gradient(90deg, rgba(255, 12, 241, 1) 0%, rgba(250, 0, 135, 1) 100%);
}

.todo-row:nth-child(4n + 3) {
  background: linear-gradient(90deg, rgba(20, 159, 255, 1) 0%, rgba(17, 122, 255, 1) 100%);
}

.todo-row:nth-child(4n + 3) {
  background: linear-gradient(90deg, rgb(113, 221, 122) 0%, rgb(229, 236, 123) 100%);
}

.todo-row:nth-child(4n + 4) {
  background: linear-gradient(90deg, rgb(216, 60, 110) 0%, rgb(199, 64, 67) 100%);
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: #fff;
}

.edit-icon {
  color: #fff;
}

@media only screen and (max-width: 600px) {
  body {
    margin: auto;
  }
  .todo-app {
    display: flex;
    flex-direction: column;
    justify-content: start;
    width: 520px;
    min-height: 600px;
    background: #161a2b;
    text-align: center;
    margin: 128px auto;
    border-radius: 10px;
    padding-bottom: 32px;
  }

  h1 {
    margin: 12px 10;
    margin-right: 7rem;
    color: #fff;
    font-size: 24px;
  }

  .complete {
    text-decoration: line-through;
    opacity: 0.4;
  }

  .todo-form {
    margin-bottom: 32px;
    margin-right: 6rem;
  }

  .todo-input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #5d0cff;
    outline: none;
    width: 250px;
    background: transparent;
    color: #fff;
  }

  .todo-input::placeholder {
    color: #e2e2e2;
  }

  .todo-button {
    padding: 16px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(90deg, rgba(93, 12, 255, 1) 0%, rgb(203, 112, 207) 100%);
    color: #fff;
    text-transform: capitalize;
  }

  .todo-input.edit {
    width: 350px;
    border: 2px solid #4672c8;
  }

  .todo-button.edit {
    border-radius: 20px 20px 20px 20px;
    background: linear-gradient(90deg, rgb(71, 116, 201) 0%, rgb(49, 31, 166) 100%);
    padding: 16px 22px;
    margin-top: 10px;
    padding: 16px 22px 16px 22px;
  }

  .todo-container {
    display: flex;
    flex-direction: row;
    position: relative;
  }

  .todo-row {
    display: flex;
    margin-right: 8rem;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    background: linear-gradient(90deg, rgba(255, 118, 20, 1) 0%, rgba(255, 84, 17, 1) 100%);
    padding: 16px;
    border-radius: 5px;
    width: 70%;
  }

  .todo-row:nth-child(4n + 4) {
    margin-right: 8rem;
    background: linear-gradient(90deg, rgba(93, 12, 255, 1) 0%, rgba(155, 0, 250, 1) 100%);
  }

  .todo-row:nth-child(4n + 2) {
    margin-right: 8rem;
    background: linear-gradient(90deg, rgba(20, 159, 255, 1) 0%, rgba(17, 122, 255, 1) 100%);
  }

  .todo-row:nth-child(4n + 1) {
    margin-right: 8rem;
    background: linear-gradient(90deg, rgb(113, 221, 122) 0%, rgb(229, 236, 123) 100%);
  }

  .todo-row:nth-child(4n + 3) {
    margin-right: 8rem;
    background: linear-gradient(90deg, rgb(216, 60, 110) 0%, rgb(199, 64, 67) 100%);
  }

  .icons {
    display: flex;
    align-items: center;
    font-size: 24px;
    cursor: pointer;
  }

  .delete-icon {
    margin-right: 5px;
    color: #fff;
  }

  .edit-icon {
    color: #fff;
  }
}
